import {Stack, switchClasses} from "@mui/material";
import Button from "@mui/material/Button";
import Socket from "./Socket";
import React, {useState, useEffect} from "react";
import socketIOClient from "socket.io-client";

const ENDPOINT = "http://127.0.0.1:3005";
let socket;
const ExampleComponent = (props) => {
    const [mRes, setResult] = React.useState({});
    const [socketId, setSocketId] = React.useState("");
    const [deviceId, setDeviceId] = React.useState("");
    useEffect(() => {
        const LIVE_URL = window.location.href.split('/')[0] + '//' + window.location.href.split('/')[2];
        console.log("LIVE_URL", LIVE_URL)
        socket = socketIOClient(LIVE_URL);
        socket.on("Welcome", data => {
            console.log(data);
            setSocketId(socket.id)
        });
        socket.emit("JoinUser");
        socket.on("Data", data => {
            setResult(data);
        });
        socket.on("DeviceId", data => {
            setDeviceId(data);
        });
    }, []);

    function handleClick(e) {
        socket.emit("API", {"command": mRes[e.target.id] === 1 ? "ON" : "OFF", "pinIndex": e.target.id});
    }

    return (
        <div>
            {deviceId.length > 0 ?
                <div className="App">

                    <Stack spacing={4} direction="row" style={{padding: 30}}>


                        <Button variant={mRes?.D2 === 1 ? "outlined" : "contained"} id="D2"
                                onClick={handleClick}>D2</Button>
                        <Button variant={mRes?.D3 === 1 ? "outlined" : "contained"} id="D3"
                                onClick={handleClick}>D3</Button>


                    </Stack>
                    <Stack spacing={4} direction="row" style={{padding: 30}}>
                        <Button variant={mRes?.D4 === 1 ? "outlined" : "contained"} id="D4"
                                onClick={handleClick}>D4</Button>
                        <Button variant={mRes?.D5 === 1 ? "outlined" : "contained"} id="D5"
                                onClick={handleClick}>D5</Button>
                    </Stack>
                    <Stack spacing={4} direction="row" style={{padding: 30}}>
                        <Button variant={mRes?.D6 === 1 ? "outlined" : "contained"} id="D6"
                                onClick={handleClick}>D6</Button>
                        <Button variant={mRes?.D7 === 1 ? "outlined" : "contained"} id="D7"
                                onClick={handleClick}>D7</Button>
                    </Stack>
                    <Stack spacing={4} direction="row" style={{padding: 30}}>
                        <Button variant={mRes?.D8 === 1 ? "outlined" : "contained"} id="D8"
                                onClick={handleClick}>D8</Button>
                        <Button variant={mRes?.D9 === 1 ? "outlined" : "contained"} id="D9"
                                onClick={handleClick}>D9</Button>
                    </Stack>
                    <div>My Socket ID : {socketId}</div>
                    <div>Device ID : {deviceId}</div>
                </div> :
                <div>NO Device Connected</div>


            }
        </div>)
}

export default ExampleComponent;
